.sectionSidebar {
  box-sizing: border-box;
  height: 100%;
  background-color: var(--sidebar);
  max-width: 225px;
  width: 100%;

  @media screen and (min-width: 768px) {
    max-width: 289px;
  }

  @media screen and (min-width: 1440px) {
    display: block;
  }

  &__blur {
    width: calc(100vw - 225px);
    //width: 100%;
    height: 100%;
    backdrop-filter: blur(2px);
    position: absolute;
    left: 225px;

    @media screen and (min-width: 768px) {
      width: calc(100vw - 289px);
      left: 289px;
    }
    @media screen and (min-width: 1440px) {
      display: none;
    }
  }

  &__container {
    padding: 24px 20px;
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media screen and (min-width: 768px) {
      padding: 24px 32px;
    }
  }

  &__title {
    font-family: 'Inter';
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    color: var(--sidebar-text-panel);
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.21;
      margin-bottom: 32px;
    }
  }
  &__logoBtn {
    display: inline-flex;
    align-items: center;
    gap: 39px;
    margin-bottom: 64px;

    @media screen and (min-width: 768px) {
      gap: 36px;
      margin-bottom: 50px;
    }
    @media screen and (min-width: 1440px) {
      margin-bottom: 33px;
    }
  }
  &__closeBtn {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    &__closeIcon {
      display: block;
      fill: transparent;
      width: 24px;
      height: 24px;
      stroke-width: 0.9599609375em;
      stroke: var(--name-text-color);

      @media screen and (min-width: 768px) {
        width: 34px;
        height: 34px;
      }
      @media screen and (min-width: 1440px) {
        display: none;
      }
    }
  }
}
